<template>
  <el-container>
    <el-header>
      <div id="header">
        DesignColors 相近色配色工具
      </div>
    </el-header>
    <el-main style="height: 70vh">
      <div class="pickColor">

        <br><br>
        <span>在此选择颜色<i class="el-icon-set-up"></i></span>
        <br><br>
        <el-color-picker v-model="color1" color-format="hsl"></el-color-picker>
        <br><br>
        可选的颜色
      </div>

      <div id="main">
        <br>
        <br><br><br><br><br>
        <div id="colorItems">


          <div id="colorItem1">
            <div id="color1" v-bind:style="{backgroundColor:color1}"></div>
            <div id="colorSpan1">{{color1}}</div>
          </div>
          <div id="colorItem2">
            <div id="color2" v-bind:style="{backgroundColor:color2}"></div>
            <div id="colorSpan2">{{color2}}</div>
          </div>
          <div id="colorItem3">
            <div id="color3" v-bind:style="{backgroundColor:color3}"></div>
            <div id="colorSpan3">{{color3}}</div>
          </div>
          <div id="colorItem4">
            <div id="color4" v-bind:style="{backgroundColor:color4}"></div>
            <div id="colorSpan4">{{color4}}</div>
          </div>
          <div id="colorItem5">
            <div id="color5" v-bind:style="{backgroundColor:color5}"></div>
            <div id="colorSpan5">{{color5}}</div>
          </div>
        </div>
      </div>
    </el-main>
    <el-footer>
      <el-alert
          title="你可以点击下方“转到色谱”查看颜色列表并选择其中一个颜色作为基准色"
          description="输入十六进制色值后，请按2次回车以转为HSL色彩空间"
          type="warning"
          show-icon>
      </el-alert>
      <br>
      <a href="https://www.designcolors.cc" style="color: darkgray;"><i class="el-icon-link
"></i>转到色谱</a>
      <br><br>
      {{copyright}}
    </el-footer>
  </el-container>
</template>

<script>
export default {
  name: 'HelloWorld',
  props: {
    msg: String
  },
  computed:{
    copyright:function (){
        let date = new Date()
        var start = "2022"
        var yearNow = date.getFullYear()
        return "©Designcolors开发组 Zhao "+start+"~"+yearNow;
    },
    h:function (){
        let temp = this.color1.split("(")
        let temp2 = temp[1].split(",")[0]
        return temp2
    },
    s:function (){
      let temp = this.color1.split("(")
      let temp2 = temp[1].split(",")[1]
      return temp2
    },
    l:function (){
      let temp = this.color1.split("(")
      let temp2 = temp[1].split(",")[2]
      let temp3 = temp2.split(")")[0]
      return temp3
    },
    color2:function (){
        return "hsl("+(Number(this.h)-15).toString()+","+this.s+","+this.l+")"
    },
    color3:function (){
      return "hsl("+(Number(this.h)+15).toString()+","+this.s+","+this.l+")"
    },
    color4:function (){
      return "hsl("+(Number(this.h)+35).toString()+","+this.s+","+this.l+")"
    },
    color5:function (){
      return "hsl("+(Number(this.h)+50).toString()+","+this.s+","+this.l+")"
    },

  },
  data(){
    return{
      color1:"hsl(0,100%,50%)",
    }
  }
}
</script>

